
html,
body {
  overflow-x: hidden;
}

body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

@media(max-width: 767px) {
  body {
    justify-content: flex-start;
  }
}

.center {
  max-width: 1170px;
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;
  padding: 20px 15px;
}

*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0;
}

p {
  margin: 0;
}


.loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0,0,0,.3);
  z-index: -99;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  &::before {
    content: url(../img/loader.svg);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  &.active {
    z-index: 99;
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
}

.disable_page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: white;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 110%;
  color: #0F0E0E;

  & span {
    display: block;
    font-weight: normal;
  }

  &.active {
    display: flex;
  }
}

div.wpcf7 .ajax-loader {
  background-image:url(/wp-content/themes/yescards/img/loader.svg);
  width:200px;
  height:200px;
  display:block;
  position:fixed!important;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
  z-index:99999
}

div.wpcf7 .ajax-loader:before {
  content:'';
  position:fixed!important;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
  z-index:99990;
  width:100%;
  height:100%;
  background:#fff;
  opacity:.2
}

#disabled {
	display: none;
	&.active {
    display: block
  }
}

.overlay {
	visibility: hidden;
	opacity: 0;
	width: 100%;
	height: 100vh;
	background: #fff;
	z-index: -99;
	position: fixed;
	top: 0;
	left: 0;
  transition: all .2s;
}

.modal-active .overlay {
	visibility: visible;
	opacity: 1;
	transition: all .2s;
  z-index: 99;
}
.visible {
	transform: translate(-50%, -50%);
	position: fixed;
	left: 50%;
	top: 50%;
	z-index: 999;
  text-align: center;

	& .title {
		margin-bottom: 29px;
		font-size: 27px;
		line-height: 1.4;
    color: #100F0F;
  }
}

.modals button.close {
	position: fixed;
	right: 42px;
	top: 42px;
	border: none;
	background: transparent;
	opacity: 1;
  z-index: 99991;

	& img {
		width: 35px;
    height: 35px;
  }
}
