.result.question {
  padding: 45px 35px;
}

.question {
  background: #F6F6F6;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 45px 92px;
  max-width: 600px;
  box-sizing: border-box;
  margin-top: 30px;

  &.active {
    display: flex;
  }
}

.question_steps {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  color: #000000;
  margin-bottom: 12px;
}

.question_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 120%;
  color: #000000;
  margin-bottom: 34px;
  display: inline-block;
  text-align: center;
}

.result .question_body {
  flex-direction: column;
  align-items: center;
  padding: 0 65px;
}

.question_body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.question_checkbox {
  display: none;
}

.question_checkbox:checked + .question_answer {
  background: #CB3D54;
  color: white;
}

.question_answer {
  flex-basis: 200px;
  flex-grow: 0;
  flex-shrink: 0;
  flex-shrink: 0;
  background: #E7E7E7;
  border-radius: 5px;
  padding: 17px 6px;
  margin-bottom: 16px;
  box-sizing: border-box;
  text-align: center;

  &:hover {
    cursor: pointer;
  }

  &:nth-of-type(odd) {
    margin-right: 16px;
  }
}

.question_text-input {
  background: #DEDEDE;
  border-radius: 4px;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: black;
  width: 100%;
  padding: 17px 20px;
  margin-bottom: 10px;
  box-sizing: border-box;

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat, #DEDEDE;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat, #DEDEDE;
  }

  &::placeholder {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 120%;
    color: #6C6C6C;
  }
}

.question_attention {
  display: inline-block;
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-top: 6px;
  margin-bottom: 16px;
}

.question_protection {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120%;
  color: #BD4D4E;
  margin-bottom: 16px;

  & img {
    margin-right: 12px;
  }
}

#accepting {
  display: none;
}

#accepting:checked ~ .question_accepting::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.799805 3.99971L4.53314 6.79971L9.19981 1.19971' stroke='%232B4FA9'/%3E%3C/svg%3E%0A") center no-repeat;
}

.question_accepting {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 120%;
  text-align: center;
  color: #2B4FA9;
  position: relative;
  display: inline-block;
  padding-left: 22px;
  margin-bottom: 22px;

  &:hover {
    cursor: pointer;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 14px;
    height: 14px;
    border: 1px solid #2B4FA9;
    background: none;
    position: absolute;
    top: calc(50% - 7px);
    left: 0;
  }
}

.first-question .question_pagination,
.result .question_pagination {
  justify-content: center;
}

.question_pagination {
  margin-top: 18px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}

.result .question_next {
  flex-basis: 280px;
}

.question_next {
  background: #15AF40;
  box-shadow: 0px 0px 50px rgba(21, 175, 64, 0.6);
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 18px 0;
  flex-basis: 160px;

  &.disabled {
    opacity: 0.3;
    pointer-events: none;

    &:hover {
      cursor: auto;
    }
  }

  &:hover {
    cursor: pointer;
  }
}

.question_prev {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 120%;
  color: #2B4FA9;
  margin-bottom: 1px;
  border-bottom: 1px solid #2B4FA9;

  &:hover {
    cursor: pointer;
  }
}

.valid-error {
  color: #CB3D54;
  font-size: 11px;
  display: inline-block;
  width: 100%;
  text-align: center;
}

@media(max-width: 767px) {
  .result.question {
    padding: 24px 12px;
  }

  .question {
    padding: 24px 12px;
    max-width: none;
    margin-right: 15px;
    margin-left: 15px;
    width: calc(100% - 30px);
  }

  .question_steps {
    font-size: 14px;
    line-height: 120%;
    margin-bottom: 8px;
  }

  .question_title {
    font-size: 18px;
    line-height: 120%;
    margin-bottom: 24px;
  }

  .result .question_body {
    padding: 0;
  }

  .question_body {
    flex-direction: column;
    width: 100%;
    padding: 0 13px;
    box-sizing: border-box;
  }

  .question_answer {
    flex-basis: auto;
    width: 100%;
    margin-bottom: 10px;

    &:nth-of-type(odd) {
      margin-right: 0;
    }
  }

  .question_pagination {
    margin-top: 14px;
    padding-left: 13px;
  }

  .question_text-input {
    margin-bottom: 10px;
  }

  .question_attention {
    font-size: 11px;
  }

  .result .question_pagination {
    padding-left: 0;
  }

  .question_next {
    flex-basis: 170px;
  }

  .result .question_next {
    width: 100%;
    box-sizing: border-box;
  }
}
